<template>



  <transition name="video">
    <div
      v-if="showScreensaver"
      class="video-container d-flex justify-content-center align-items-center"
    >





      <transition name="mute">
        <div v-if="!muteButtonHide" @click="toggleMute" class="button-mix">
          <img id="volume-button" :src="muteButton" alt="" /></div
      ></transition>
      <video
        @click="clearScreensaver"
        playsinline
        autoplay
        muted
        loop
        id="myVideo"
        :class="{ blur: showScreensaver }"
        preload="none"
      >
        <source
          src="https://d3kc4mzo6omdrt.cloudfront.net/om-hd.mp4"
          type="video/mp4"
        />
      </video></div
  ></transition>

  <transition name="load">
    <div v-if="loaded" class="loading-screen">
      <div
        class="container-fluid h-100 d-flex justify-content-center align-items-center flex-column"
      >
        <div :style="{ top: count + '%' }" class="scan-reveal"></div>
        <img class="loading-image" src="@/assets/img/om-loading.jpg" alt="" />
      </div>
    </div>
  </transition>

  <router-view  v-slot="slotProps">
    <transition name="route" mode="out-in" appear>
      <component :is="slotProps.Component"></component>
    </transition>
  </router-view>
</template>

<script>
import "lazysizes";
export default {
  data() {
    return {
      count: 1,
      loaded: true,
      showScreensaver: false,
      time: [],
      displayNav: false,
      t: null,
      muteButton: require("@/assets/img/volume-mute.svg"),
      muteButtonHide: false,


    };
  },
  methods: {
    preloader() {
      let play = this;

      // Loads the images one at a time, then calls the callback function when all images
      // have been loaded
      const loadImages = (index, callback) => {
        if (index < 40) {
          this.count = index * 2.5;
          let img = new Image();
          img.src = require("@/assets/img/icons/" + [index] + ".png");

          img.onload = function () {
            loadImages(++index, callback);
          };
        } else {
          callback();
        }
      };
      window.onload = function () {
        loadImages(1, () => {
          setInterval(() => {
            play.loaded = false;
            play.idleChecker();
          }, 100);

          play.resetTimer;
        });
      };
    },
    startSreensaver() {
      this.showScreensaver = true;
    },
    toggleMute() {
      
      var video = document.getElementById("myVideo");
      video.muted = false;

      this.muteButtonHide = true;
    },

    resetTimer() {
      if (this.showScreensaver) {
        return;
      } else {
        this.showScreensaver = false;

        clearTimeout(this.t);
        this.t = setTimeout(this.startSreensaver, 180000);
      }
    },
    clearScreensaver() {

      var video = document.getElementById("myVideo");
      video.muted = true;
      this.showScreensaver = false;
      this.muteButtonHide = false;
      this.resetTimer();
    },
    idleChecker() {
      window.onload = this.resetTimer;
      window.onmousemove = this.resetTimer;
      window.onmousedown = this.resetTimer;
      window.ontouchstart = this.resetTimer;
      window.ontouchmove = this.resetTimer;
      window.onclick = this.resetTimer;
      window.onkeydown = this.resetTimer;
      window.addEventListener("scroll", this.resetTimer, true);
      this.resetTimer;
    },
    handleLoadedContent(event) {
      this.displayNav = event;
    },
  },

  watch: {
    showScreensaver() {
      if (this.showScreensaver) {
        document.body.style.overflow = "hidden";
      } else {
        document.body.style.overflow = "auto";
      }
    },
  },
  mounted() {
  
    visualViewport.addEventListener("resize", () => {
      document.documentElement.style.setProperty(
        "--viewport-height",
        `${visualViewport.height}px`
      );
    });


  },
  created(){
      this.preloader();
  }
};
</script>



